import React from 'react';
import TheOfficeIpsum from '../images/theOfficeIpsum.jpg';
import TheHolyKitchen from '../images/theHolyKitchen.jpg';

class CardsPersonal extends React.Component {

    render() {
        return (
            <div className="cardContainer">
                <div className="card" onClick={()=>window.location.href='https://rahulperfect06.github.io/theOfficeIpsum/'}>
                    <img className="card__img" src={ TheOfficeIpsum } width="400px" height="300px" alt=""/>
                    <div className="card__text">
                        <h3 className="card__title">The Office Ipsum</h3>
                    </div>
                </div>
                <div className="card" onClick={()=>window.location.href='https://rahulperfect06.github.io/theholykitchen/'}>
                    <img className="card__img" src={ TheHolyKitchen } width="400px" height="300px" alt=""/>
                    <div className="card__text">
                        <h3 className="card__title">The Holy Kitchen</h3>
                    </div>
                </div>
            </div>       
        );
    }
}

export default CardsPersonal;